import React, {useState, useEffect} from 'react'
import topBtn from '../images/top-button.png'
const Scroll = () => {

    const [ScrollY, setScrollY] = useState(0); //  
    const [BtnStatus, setBtnStatus] = useState(false); //  
  
    const handleScroll = () => {
      setScrollY(window.pageYOffset); // window   ScrollY 
      if (ScrollY > 100) {
        setBtnStatus(true);
      } else {
        setBtnStatus(false);
      }
    };
  
    const handleTop = () => {
      //     
      window.scrollTo({
        top: 0,
        behavior: "smooth",
      });
      setScrollY(0); // ScrollY   
      setBtnStatus(false); //  
    };
  
    useEffect(() => {
      const watch = () => {
        window.addEventListener("scroll", handleScroll);
      };
      watch(); // addEventListener  
      return () => {
        window.removeEventListener("scroll", handleScroll); // addEventListener  
      };
    });

  return (
    <div>
      <button
        className={BtnStatus ? "topBtn active" : "topBtn"} //   
        onClick={handleTop} //    
      >
      <img src={topBtn} width="50px" height="50px" alt="TOP"/>
      </button>
    </div>
  )
}

export default Scroll